

.profile-more {
    /* display: none; */
    height: 70%;
    z-index: 2000;
    position: relative;
    display: none;
    top: 97.5%;
}

.expanded .profile-more {
    display: block;
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
}

.profile-more .button {
    position: absolute;
    top: 0;
    left: 85%;
}

.profile-more .back {
    background-image: url(data:image/gif;base64,)
}

.name-row {
    padding: 3%;
    text-align: left;    
}

.name-row p {
    margin: 0;
}

.name-row .name-info {
    line-height: 1.7em;
    font-size: 1.6em;
    text-align: left;
}

.share-row, .report-row {
    border-top: 1px solid #f1f1f1;
    border-bottom: 1px solid #f1f1f1;
    text-transform: uppercase;
    line-height: 1.2em;
    font-size: 1em;
    color: #fd5068;
    padding: 3.5%;
    letter-spacing: 0.15em;
}

.share-row p {
    margin: 0;
}

.share-row span {
    display: block;
}

.share-row .second-line {
    line-height: 1.2em;
    font-size: 0.8em;    
}

.report-row {
    padding: 0;
    color: #dadada;
}

.description-row {
    height: 50%;
    text-align: left;
    padding: 3%;    
}

.expanded .profile-more .button  {
    will-change: top;
    -webkit-animation: 0.7s bounce;
            animation: 0.7s bounce;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}

@-webkit-keyframes bounce {
    0% { 
        top: 0%;
    }
    25% { 
        top: -2%;
    }
    50% {
        top: 0.75%;
    }
    75% {
        top: -0.5%;
    }
    100% {
        top: 0%;
    }
}

@keyframes bounce {
    0% { 
        top: 0%;
    }
    25% { 
        top: -2%;
    }
    50% {
        top: 0.75%;
    }
    75% {
        top: -0.5%;
    }
    100% {
        top: 0%;
    }
}


