.App-profile {
    height: 91%;
    width: 100%;
    position: absolute;
    bottom: 0;
    background:-webkit-gradient(linear, left top, left bottom, from(#f4f4f4), to(#f7f8fb));
    background:-webkit-linear-gradient(top, #f4f4f4, #f7f8fb);
    background:-o-linear-gradient(top, #f4f4f4, #f7f8fb);
    background:linear-gradient(to bottom, #f4f4f4, #f7f8fb);
    overflow-y: auto;
    overflow-x: hidden;
}

.content {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: translate3d(0, 0, 0) scale(1);
            transform: translate3d(0, 0, 0) scale(1);    
    -webkit-transition: 0.2s;    
    -o-transition: 0.2s;    
    transition: 0.2s;
    -ms-touch-action: none;
        touch-action: none;
}

.expanded .content {
    -webkit-transform: translate3d(0, -35%, 0) scale(1.1);
            transform: translate3d(0, -35%, 0) scale(1.1);
    -ms-touch-action: initial;
        touch-action: initial;
}

.expanded .navigator li {
    -webkit-transform: translateY(4500%) scale(0.8);
        -ms-transform: translateY(4500%) scale(0.8);
            transform: translateY(4500%) scale(0.8);
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s
}

.goNext .draggable-frame {
    opacity: 0;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s    
}

.goNext .draggable-frame.right { 
    -webkit-transform: translate3d(150%, 10%, 0) rotateZ(-25deg) !important; 
            transform: translate3d(150%, 10%, 0) rotateZ(-25deg) !important;
}

.goNext .draggable-frame.up { 
    -webkit-transform: translate3d(0%, -150%, 0) rotateZ(-5deg) !important; 
            transform: translate3d(0%, -150%, 0) rotateZ(-5deg) !important;
}

.goNext .draggable-frame.left { 
    -webkit-transform: translate3d(-150%, 10%, 0) rotateZ(25deg) !important; 
            transform: translate3d(-150%, 10%, 0) rotateZ(25deg) !important;
}

.next {
    -webkit-transform: scale(0.8);
        -ms-transform: scale(0.8);
            transform: scale(0.8);
    opacity: 0;
    position: absolute;
    width: 100%;
}

.goNext .next {
    opacity: 1;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);    
}


