
.overlay {
    width: 100%;
    height: 30%;
    position: absolute;
    bottom: -1%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: white;
    opacity: 1;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

.expanded .overlay {
    display: none;
    opacity: 0;
}

.overlay .name-info {
    position: absolute;
    left: 5%;
    bottom: 12%;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 1.8em;    
}

.overlay .button {
    position: absolute;
    right: 5%;
    bottom: 25%;
    top: initial;
    left: initial;
    -webkit-transform: initial;
        -ms-transform: initial;
            transform: initial;
}


