
@import '../../../ui/components/css/bars.css';

.buttons {
    position: fixed;
    height: 15%;
    opacity: 1;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

.expanded .buttons {
    z-index: 5000;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#FFFFFF), color-stop(#FFFFFF), color-stop(rgba(255,255,255,0)), to(rgba(255,255,255,0)));
    background-image: -webkit-linear-gradient(bottom, #FFFFFF, #FFFFFF, rgba(255,255,255,0), rgba(255,255,255,0));
    background-image: -o-linear-gradient(bottom, #FFFFFF, #FFFFFF, rgba(255,255,255,0), rgba(255,255,255,0));
    background-image: linear-gradient(to top, #FFFFFF, #FFFFFF, rgba(255,255,255,0), rgba(255,255,255,0));
}

.expanded .buttons .cell {
    will-change: opacity;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
    opacity: 1;
}

.expanded .buttons .cell:first-child {
    opacity: 0;
}

.expanded .buttons .cell:last-child {
    opacity: 0;
}

.buttons.down {
    bottom: 0;
}




