
.photos {
    width: 98%;
    height: 100%;
    clear: both;
    position: absolute;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 1%;
}

.photos .navigator {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 95%;
    margin-left: 2.5%;    
}

.photos .navigator li {
    float: left;
    line-height: 25%;
    height: 1%;
    margin: 0.5%;
    margin-top: 1.5%;    
    -webkit-box-sizing: border-box;    
            box-sizing: border-box;
    border-radius: 10%;
    background-color: #aaaaaa;
    opacity: 0.3;    
}

.photos .navigator li.active {
    background-color: white;
    opacity: 1;
}

.photos .navigator li.sub-2 {
    width: 48.5%;
}

.photos .navigator li.sub-3 {
    width: 32%;
}

.photos .navigator li.sub-4 {
    width: 24%;
}

.photos .navigator li.sub-5 {
    width: 19%;
}

.photos .navigator-content {
    height: 100%;
    display: none;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: none;
    border-radius: 1.5%;
    -webkit-box-shadow: 0px 5px 15px rgba(20, 20, 20, 0.3);
            box-shadow: 0px 5px 15px rgba(20, 20, 20, 0.3)
}

.expanded .photos .navigator-content {
    -webkit-box-shadow: none;
            box-shadow: none;    
}

.photos .navigator-content.active {
    display: block;
}


