
.button {
    width:60px;
    height: 60px;
    line-height: 70px;
    overflow:hidden;
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
}

.button.rounded {
    border-radius: 50%;
    -webkit-border-image: -webkit-linear-gradient(45deg, #f0f2f5, #f4f5f7);
         -o-border-image: -o-linear-gradient(45deg, #f0f2f5, #f4f5f7);
            border-image: linear-gradient(45deg, #f0f2f5, #f4f5f7);
    -webkit-box-shadow: 0px 5px 50px rgba(20, 20, 20, 0.1);
            box-shadow: 0px 5px 50px rgba(20, 20, 20, 0.1);    
}

.button.small {
    width: 50px;
    height: 50px;
    line-height: 50px;
}

.button.smaller {
    width: 22px;
    height: 22px;
    line-height: 22px;
    font-size: 1em;
}

.button i.icon {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    background-size: contain;
    background-position: 50%;
    background-repeat: no-repeat;    
}

.button i.icon.refresh {
    background-image: url(data:image/gif;base64,)
}

.button i.icon.nope {
    background-image: url(data:image/gif;base64,)
}

